import Loading from '../../public/Loading'
import test from '../../../static/js/apy.js'
  export default {
      name: "withdrawal",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          bankList:[],
          clickIndex:0,
          de_banlece:'',
          isAddcard:false,
          card:'',
          name:"",
          bank:'',
          banKAllList:[],
          monney:'',
          isPassword:false,
          password:'',
          aindex:0,
          isWait:false
        }
    },
    components:{
      Loading,
    },
    watch:{
      password:function(){
        if(this.password.length == 6){
          this.sendDraw()
          this.aindex = 0
        }
      }
    },
    methods:{
      chooseBank(item,index){
        this.clickIndex = index
      },
      getbank(){
        this.$http.post(this.api + '/bankl',{
          token: localStorage.getItem('token'),
        })
          .then(res=>{
            if(res.data.code == 1){
              this.bankList = res.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getbank()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      addBank(){
        this.isAddcard = true
      },
      saveBank(){
        if(!this.card){
          this.$message({type:'info',message:'请填写银行卡号'})
        }else if(!this.name){
          this.$message({type:'info',message:'请填写用户姓名'})
        }else if(!this.bank){
          this.$message({type:'info',message:'请填写银行名称'})
        }else{
          this.isWait = true
          this.$http.post(this.api + '/banks',{
            token: localStorage.getItem('token'),
            bank_name: this.bank,
            card_number: this.card,
            user_name: this.name
          })
            .then(res=>{
              if(res.data.code == 1){
                this.getbank()
                this.isAddcard = false
                this.isWait = false
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/Index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.saveBank()
                  })
              }else if(res.data.code == 0){
                this.isWait = false
                alert(res.data.msg)
              }
            })
        }
      },
      refer(){
        let reg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        if(reg.test(this.monney) && parseInt(this.monney)>200){
          this.isPassword = true
          this.pay()
        }else{
          this.$message({
            type:'info',
            message:'请填写正确金额！（最多保留两位小数的正数）'
          })
        }
      },
      pay(){
        this.$nextTick(() => {
          this.$refs.input.focus()
        })
      },
      onFocus(){
        var list = document.getElementsByClassName('list')
        if (event.keyCode === 99 ||event.keyCode === 96 ||event.keyCode === 97||event.keyCode === 98|| event.keyCode === 99|| event.keyCode === 100|| event.keyCode ===101|| event.keyCode === 102|| event.keyCode === 103||
          event.keyCode === 104|| event.keyCode === 105 || event.keyCode === 48||event.keyCode === 49||event.keyCode === 50||event.keyCode === 51||event.keyCode === 52||event.keyCode === 53||event.keyCode === 54||
          event.keyCode === 55||event.keyCode === 56||event.keyCode === 57){
          list[this.aindex].innerHTML = '*'
          this.aindex +=1
        }else if(event.keyCode === 8){
          this.aindex -= 1
          list[this.aindex].innerHTML = ' '
        }
      },
      sendDraw(){
        this.isWait = true
        this.$http.post(this.api +'/Draw',{
          token: localStorage.getItem('token'),
          amount: this.monney,
          bank_id: this.bankChose,
          pay_password: this.password
        })
          .then(res=>{
            if(res.data.code == 1){
              this.isPassword = false
              this.password = ''
              this.isWithdrawal = false
              this.isWait = false
              this.$message({
                type:'success',
                message: '提交成功！'
              })
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.sendDraw()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isPassword = false
              this.password = ''
              this.isWait = false
            }
          })
      },
    },
    mounted(){
        this.getbank()
    },
    created(){
        this.de_banlece = this.$route.query.information
        this.banKAllList = test()
    }
  }



